import React from 'react';
import { Box } from 'rebass';

const CarouselDot = ({ index, activeIndex, setActiveIndex }) => (
  <Box
    onClick={(event) => {
      // Prevent click from going through to parent
      event.stopPropagation();
      setActiveIndex(index);
    }}
    data-testid="carousel-dot"
  >
    <Box
      variant={activeIndex === index ? 'activeDot' : 'inactiveDot'}
      m={1}
      sx={{
        width: 12,
        height: 12,
        borderRadius: 12,
        cursor: 'pointer',
      }}
    />
  </Box>
);

export default CarouselDot;
