import React from 'react';
import countries from './assets/countries.json';

const emailPattern = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}/;

const convertTextToLines = (text) => {
  if (text.includes('\n')) {
    return text.split('\n').map((t, i) => <div key={i}>{t}</div>);
  }
  return text;
};

const getCookie = (name) => {
  const match = document.cookie.match(new RegExp(`(^| )${name}=([^;]+)`));
  return match && match[2].toLowerCase() !== 'null' ? match[2] : undefined;
};

const getCountries = () => [
  { name: 'Select country', value: 'choose-country', disabled: true },
  ...countries.sort((a, b) => (a.name < b.name ? -1 : 1)),
];

export {
  emailPattern, convertTextToLines, getCookie, getCountries,
};
