import React, {
  useState, useEffect, useCallback, useRef, useLayoutEffect,
} from 'react';
import { Box, Flex } from 'rebass';
import CarouselItem from './CarouselItem';
import CarouselDot from './CarouselDot';

const CAROUSEL_DURATION = 6000;

const Carousel = ({ slides }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [timer, setTimer] = useState(null);
  const [carouselWidth, setCarouselWidth] = useState(null);
  const carouselRef = useRef();

  const advanceSlider = useCallback(() => {
    setActiveIndex((index) => (index + 1 >= slides.length ? 0 : index + 1));
  }, [slides.length]);

  const stopCarousel = useCallback(() => clearInterval(timer), [timer]);

  const startCarousel = useCallback(() => {
    setTimer(setInterval(advanceSlider, CAROUSEL_DURATION));
    return stopCarousel;
  }, [stopCarousel, advanceSlider]);

  useEffect(() => {
    startCarousel();
  }, []); // eslint-disable-line

  useLayoutEffect(() => {
    const handleResize = () => {
      setCarouselWidth(carouselRef.current.offsetWidth);
    };

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, []);
  return (
    <Box
      data-testid="carousel"
      variant="carousel"
      onMouseEnter={stopCarousel}
      onMouseLeave={startCarousel}
      onClick={advanceSlider}
      ref={carouselRef}
      sx={{
        width: '100%',
        position: 'relative',
        overflow: 'hidden',
        display: ['none', 'none', 'block'],
        cursor: 'pointer',
      }}
    >
      <Flex sx={{
        transition: 'transform 0.5s',
        transform: `translate3d(-${carouselWidth * activeIndex}px, 0, 0)`,
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        width: carouselWidth * slides.length,
      }}
      >
        {slides.map((item) => (
          <CarouselItem
            key={item.id}
            item={item}
            carouselWidth={carouselWidth}
          />
        ))}
      </Flex>
      <Flex sx={{
        position: 'absolute', left: 32, bottom: 32, zIndex: 1,
      }}
      >
        {slides.map((item, index) => (
          <CarouselDot
            key={index}
            index={index}
            activeIndex={activeIndex}
            setActiveIndex={setActiveIndex}
          />
        ))}
      </Flex>
    </Box>
  );
};

export default Carousel;
