import React, {
  useEffect, useState, useRef, useLayoutEffect,
} from 'react';
import {
  Box, Text, Flex, Link,
} from 'rebass';
import Player from '@vimeo/player';
import { LoadingDots } from 'lp-react-components';
import Logo from './Logo';
import { ReactComponent as ErrorIcon } from '../assets/icon-error.svg';
import { ReactComponent as CreatingIcon } from '../assets/icon-creating.svg';
import { ReactComponent as SuccessIcon } from '../assets/icon-success.svg';
import { convertTextToLines } from '../utils';

const MESSAGE_STATES = {
  creating: {
    icon: <CreatingIcon data-testid="creatingIcon" style={{ width: 44, height: 44 }} />,
    title: 'Setting up your account',
    description: 'Please wait while we configure and set your account. In the meanwhile, please watch our product overview video.',
  },
  success: {
    icon: <SuccessIcon data-testid="successIcon" style={{ width: 44, height: 44 }} />,
    title: 'Congratulations! Your 45 day trial account has been created',
  },
  error: {
    icon: <ErrorIcon data-testid="errorIcon" style={{ width: 44, height: 44 }} />,
    title: 'Oops! Something went wrong',
    description: 'Get ready to explore and build amazing conversational experiences for your business.',
    retryMessage: 'Retry to sign up again',
  },
};

const getSize = (headerHeight, headerWidth, logoHeight) => {
  const newHeight = window.innerHeight - headerHeight - logoHeight - 40;
  if (newHeight / 0.5625 > headerWidth) {
    return {
      width: headerWidth,
      height: headerWidth * 0.5625,
    };
  }
  return {
    width: newHeight / 0.5625,
    height: newHeight,
  };
};

const Video = ({
  isVideoActive,
  messageState,
  accountDetails,
  resetRegistration,
}) => {
  const [isVideoReady, setIsVideoReady] = useState(false);
  const [dimensions, setDimensions] = useState({});
  const playerRef = useRef();
  const headerRef = useRef();
  const logoRef = useRef();
  const playerContainer = useRef();
  const isSuccess = messageState === 'success';
  const isCreating = messageState === 'creating';
  const {
    icon, title, description, retryMessage,
  } = MESSAGE_STATES[messageState];

  useLayoutEffect(() => {
    const handleResize = () => {
      const headerHeight = headerRef.current.offsetHeight;
      const headerWidth = headerRef.current.offsetWidth;
      const logoHeight = logoRef.current.offsetHeight;
      setDimensions(getSize(headerHeight, headerWidth, logoHeight));
    };

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, [messageState]);

  useEffect(() => {
    playerRef.current = new Player('video');
    setIsVideoReady(true);
  }, []);

  useEffect(() => {
    if (isVideoReady && isVideoActive) {
      playerRef.current.play();
    }
  }, [isVideoReady, isVideoActive]);

  return (
    <Box
      backgroundColor="navy"
      sx={{
        height: '100%',
        flex: 1,
        overflow: 'auto',
        display: 'grid',
        gridTemplateRows: 'auto 1fr',
      }}
    >
      <Box ref={logoRef}>
        <Logo isStatic />
      </Box>
      <Flex
        flexDirection="column"
        alignItems="center"
        mx={[4, 6, 7]}
        pb={6}
        px={[4, 6, 7]}
        sx={{ textAlign: 'center', height: '100%' }}
      >
        <Box ref={headerRef} width="100%">
          {icon}
          {title && (
          <Text variant="videoTitle" my={3}>
            {title}
            {isCreating ? <LoadingDots /> : null}
          </Text>
          )}
          {description && (
          <Text variant="videoDescription" mb={6} mx={[2, 4, 6]}>
            {convertTextToLines(description)}
            {retryMessage && (
              <Text
                data-testid="retryButton"
                onClick={resetRegistration}
                mt={4}
                mb={6}
                ml={1}
                fontSize={1}
                sx={{
                  display: 'inline',
                  color: 'aqua',
                  cursor: 'pointer',
                  fontWeight: 700,
                  '&:hover': {
                    color: 'aquaHover',
                  },
                }}
              >
                {retryMessage}
              </Text>
            )}
          </Text>
          )}
          {isSuccess && (
            <Flex mb={7} alignItems="center" mt={4} justifyContent="center" flexWrap="wrap">
              <Text variant="accountLabel" mr={3}>Account #</Text>
              <Text variant="accountDetail" mr={6}>{accountDetails.accountId}</Text>
              <Text variant="accountLabel" mr={3}>Username</Text>
              <Text variant="accountDetail" mr={7}>{accountDetails.username}</Text>
              <Link href="https://authentication.liveperson.net/login.html" target="_blank" variant="primaryLink">Login to your account</Link>
            </Flex>
          )}
        </Box>
        <Box flex={1} sx={{ width: '100%' }} ref={playerContainer}>
          <Box
            sx={{
              margin: '0 auto',
              position: 'relative',
              height: dimensions.height,
              width: dimensions.width,
              outlineColor: 'shadow',
              outlineWidth: '6px',
              outlineStyle: 'solid',
            }}
          >
            <iframe
              title="FTUE"
              ref={playerRef}
              id="video"
              data-testid="video"
              src="https://player.vimeo.com/video/398468660?autoplay=0&amp;app_id=122963"
              frameBorder="0"
              allow="autoplay; fullscreen"
              allowFullScreen
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
              }}
            />
          </Box>
        </Box>
      </Flex>
    </Box>
  );
};

export default Video;
