import React, { useState } from 'react';
import { Box, Text, Link } from 'rebass';
import Carousel from './Carousel';
import Logo from './Logo';
import carouselData from '../assets/carouselData.json';
import Header from './Header';
import RegistrationForm from './RegistrationForm';
import Video from './Video';
import Title from './Title';

const App = () => {
  const [messageState, setMessageState] = useState('creating');
  const [isVideoActive, setIsVideoActive] = useState(false);
  const [accountDetails, setAccountDetails] = useState({});
  const [userDetails, setUserDetails] = useState(
    {
      firstName: '',
      lastName: '',
      email: '',
      password: '',
      region: '',
      recaptchaResponseToken: '',
    },
  );

  const resetRegistration = () => {
    setMessageState('creating');
    setIsVideoActive(false);
    setAccountDetails(null);
    // Reset password
    setUserDetails({ ...userDetails, password: '' });
  };

  if (isVideoActive) {
    return (
      <Video
        isVideoActive={isVideoActive}
        messageState={messageState}
        accountDetails={accountDetails}
        resetRegistration={resetRegistration}
      />
    );
  }

  return (
    <Box
      sx={{
        height: '100%',
        position: 'relative',
        display: ['block', 'block', 'grid'],
        gridTemplateColumns: '5fr 2fr 9fr',
      }}
      mx={[5, 6, 0]}
    >
      <Logo />
      <Carousel slides={carouselData} />
      <Box backgroundColor="white" />
      <Box sx={{ flex: 1, overflow: 'auto' }} data-testid="registerContent">
        <Header />
        <Title />
        <RegistrationForm
          setIsVideoActive={setIsVideoActive}
          setMessageState={setMessageState}
          setAccountDetails={setAccountDetails}
          userDetails={userDetails}
          setUserDetails={setUserDetails}
        />
        <Text variant="privacyPolicy" mt={6} mb={7}>
          By creating an account, I agree to LivePerson&apos;s&nbsp;
          <Link
            sx={{
              textDecoration: 'none',
              color: 'electricBlue',
              '&hover': {
                color: 'electricBlueHover',
              },
            }}
            href="https://www.liveperson.com/policies/license-agreement/"
          >
            End User License Agreement
          </Link>
        </Text>
      </Box>
    </Box>
  );
};

export default App;
